.App {
  text-align: center;
  font-family: Arial, sans-serif;
  margin-top: 20px;
}

.logo {
  width: 350px;
  height: auto;
  margin-bottom: 5px;
}

h5 {
  margin: 10px 0;
}

.search-bar {
  display: inline-flex;
  align-items: center;
  margin-bottom: 20px;
}

.search-bar input {
  padding: 10px;
  font-size: 16px;
  width: 300px;
  border: 1px solid #ffd700;
  border-right: none;
  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px;
  border-top-right-radius: 0px;
  border-bottom-right-radius: 0px;
  background: #27252e;
  color:#fbfbfb;
}

.search-bar button:focus {
  border: 1px solid #ffcc00;
}

.search-bar button:active {
  border: 1px solid #ffcc00;
}

.search-bar button:hover {
  border: 1px solid #ffcc00;
}


.search-bar button {
  padding: 10px 20px;
  background-color: #FFD700; /* Yellow color */
  color: #333;
  font-size: 16px;
  font-weight: bold;
  border: 1px solid #FFD700;
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
  cursor: pointer;
}

.search-bar button:hover {
  background-color: #ffcc00;
}

.card-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.card-item {
  margin: 10px;
  text-align: center;
  border: 1px solid #19181d;
  border-radius: 10px;
  max-width: 500px;
  background: #1f1f30;
}


.card-item img {
  max-width: 100%;
  height: auto;
  border-radius: 5px;
}


.card-name {
color:#d70000;
text-align: left;
padding: 0px 10px;
}

.card-type {
  color:#f5f5f5;
  text-align: left;
  padding: 0px 10px;
  }

 .card-desc
{
  color:#f9f9f9;
  text-align: left;
  padding: 0px 5px;
}